<template>
  <div class="packet">
    <img src="../../static/images/hb-get.png" class="hb">
    <img src="../../static/images/open.png" class="open" :class="{'kai':ifopen}" @click="kai">
  </div>
</template>

<script>
  export default {
    name: 'packet',
    data() {
      return {
        ifopen: false
      }
    },
    methods: {
      kai() {
        var that = this;
        that.ifopen = true;
        setTimeout(function(){
            // that.$router.replace('/cash');
            that.$router.push('/cash');
        }, 1200);
      }
    }
  }

</script>

<style scoped>
  @-webkit-keyframes animated-open {
    0% {
      -webkit-transform: rotateY(0);
      transform: rotateY(0);
    }
    100% {
      -webkit-transform: rotateY(360deg);
      transform: rotateY(360deg);
    }
  }

  .kai {
    -webkit-animation: animated-open 1s linear both;
    animation: animated-open 1s linear both;
  }

  .packet {
    width: 50%;
    margin: 1rem auto 0;
    position: relative;
  }

  .hb {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
  }

  .open {
    position: absolute;
    top: 1.5rem;
    left: 35%;
    width: 30%;
  }

</style>



