<template>
  <div class="get">
    <img :src='slogan' class="slogan">
	<Packet></Packet>
  </div>
</template>

<script>
  import axios from 'axios';
  import Packet from '@/components/packet';
  export default {
    name: 'get',
    data() {
      return {
        slogan: '../../static/images/slogan.png'
      }
    },
    components: {
      Packet
    }
  }

</script>

<style scoped>
  .get {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
	left: 0;
	background: #009040;
  }

  .slogan {
    width: 70%;
    display: block;
    margin: 1.5rem auto 0;
  }

</style>



