import Vue from 'vue';
import Router from 'vue-router';
// import HelloWorld from '@/components/HelloWorld'
import Qr from '@/pages/qr';
import Get from '@/pages/get';
import Cash from '@/pages/cash';

Vue.use(Router);

export default new Router({
  routes: [{
    path: '/',
    name: 'Qr',
    component: Qr
  }, {
    path: '/get',
    name: 'Get',
    component: Get
  }, {
    path: '/cash',
    name: 'Cash',
    component: Cash
  }]
})



